<template>
  <div class="warps">
    <my-el-header title="" />
    <div class="container">
      <div class="container-l">
        <e-tree @treeClick="treeClick" :data="treeArr" />
      </div>
      <div class="container-r">
        <div class="form-nav">
          <el-form :inline="true" :close-on-click-modal="false" :model="form" class="demo-form-inline" :rules="form"
                   ref="form">
            <el-form-item label="" prop="diaplanRateGroup">
              <el-input v-model="form.routerName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label=" " prop="diaplanRateGroup">
              <el-select v-model="form.desType" placeholder="">
                <el-option
                  v-for="item in meetingState"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="  " prop="diaplanRateGroup">
              <el-select v-model="form.meetingType" placeholder="">
                <el-option
                  v-for="item in meetingState"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="find"></el-button>
              <el-button @click="clear"></el-button>
            </el-form-item>
          </el-form>
          <el-button type="primary" @click="showAddForm(null, '')"
          >
          </el-button
          >
        </div>

        <el-table
          :data="list"
          style="width: 100%">
          <el-table-column
            prop="date"
            label=""
            width="50">
            <template scope="scope">{{ scope.$index + 1 }}</template>
          </el-table-column>
          <el-table-column
            prop="conferenceName"
            label="">
          </el-table-column>
          <el-table-column
            prop="master"
            label="">
          </el-table-column>
          <el-table-column
            prop="seqType"
            label="">
            <template scope="scope">
              <div v-if="scope.row.seqType === 0"></div>
              <div v-else-if="scope.row.seqType !== 0"></div>
            </template>
          </el-table-column>
          <el-table-column
            prop="roundEnd"
            label="">
          </el-table-column>
          <el-table-column
            prop="startTime"
            label="">
          </el-table-column>
          <el-table-column
            prop="deptId"
            label="">
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="">
          </el-table-column>
          <el-table-column
            prop="isRecord"
            label="">
            <template scope="scope">
              <div v-if="scope.row.isRecord === 1"></div>
              <div v-else-if="scope.row.isRecord !== 1"></div>
            </template>
          </el-table-column>
          <el-table-column align="center" prop="address" label="" fixed="right" :width="$store.state.tableMixWidth">
            <template scope="scope">
              <div class="operate">
                <el-link type="info" @click="showAddForm(scope.row, '')"></el-link>
                <template>
                  <el-popconfirm title="" @confirm="removeIt(scope.row)">
                    <el-link type="info" slot="reference">
                    </el-link>
                  </el-popconfirm>
                </template>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog top="7vh" :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="addForm" :rules="rules" ref="addForm">
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="conferenceName">
            <el-input v-model="addForm.conferenceName" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.conferenceType">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.isRecord">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="deptId">
            <treeselect v-model="addForm.deptId" :multiple="false " :options="treeArr"
                        :normalizer="normalizer" />
          </el-form-item>
        </div>


        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="master">
            <el-select v-model="addForm.master" placeholder="" style="width: 100% ">
              <el-option
                v-for="item in memberList"
                :key="item.nickName"
                :label="item.nickName"
                :value="item.nickName">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="member">
            <el-select @change="selectChange" v-model="member" multiple placeholder="" style="width: 100% ">
              <el-option
                v-for="item in memberList"
                :key="item.nickName"
                :label="item.nickName"
                :value="item.nickName">
              </el-option>
            </el-select>
          </el-form-item>
        </div>

        <!--        -->
        <div>
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="seqType">
              <el-radio-group v-model="addForm.seqType">
                <el-radio :label="0"></el-radio>
                <el-radio :label="1"></el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
          <div class="width" v-if="addForm.seqType === 0">
            <el-form-item label="" :label-width="formLabelWidth" prop="time1">
              <el-date-picker
                style="width: 100%"
                @change="dataChange"
                v-model="time1"
                format="yyyy MM dd HH:mm"
                value-format="yyyy-MM-dd HH:mm"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder="">
              </el-date-picker>
            </el-form-item>
          </div>
          <div class="width" v-else-if="addForm.seqType !== 0">
            <el-form-item label="cron" :label-width="formLabelWidth" prop="icon">
              <div class="width-cron" :class="isActive ? '' : 'z-index'" style="width: 100%" @click="aClick">
                <el-input v-model="addForm.cron" ref="inputs" @focus="focus" @blur="blur" placeholder
                          style="width: 100%"></el-input>
                <vcrontab :class="isActive ? 'active' : 'feActive'" @hide="showCron=false"
                          :hideComponent="hideComponent" @fill="crontabFill"
                          :expression="expression"></vcrontab>
              </div>
              <!--            -->
              <!--            <div class="box">-->
              <!--              <el-input v-model="addForm.cron" placeholder class="inp"></el-input>-->
              <!--              <el-button type="primary" @click="showDialog"> cron</el-button>-->
              <!--            </div>-->
            </el-form-item>
          </div>
          <div class="width" v-else>
            <el-form-item label="" :label-width="formLabelWidth" prop="time">
              <el-time-picker
                is-range
                @change="timeChange"
                v-model="time"
                range-separator=""
                format="HH:mm"
                value-format="HH:mm"
                start-placeholder=""
                end-placeholder=""
                placeholder="">
              </el-time-picker>
            </el-form-item>
          </div>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth" prop="password">
            <el-input v-model="addForm.password" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div style="height: 310px"></div>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm"> </el-button>
      </div>
    </el-dialog>
    <my-footer />
  </div>
</template>

<script>
import myElHeader from "@/components/myElHeader";
import myFooter from "@/components/myFooter";
import eTree from "@/components/eTree";
import { getOrganizeList, getUserAll } from "@/newwork/system-colltroner";
import { fn } from "@/uti";
import treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
import { addMeeting, getMeeting, upDataMeeting } from "@/newwork/call-router";
import vcrontab from "@illidanj/cron-editor";

export default {
  name: "callMeeting",
  components: {
    myElHeader,
    myFooter,
    eTree,
    treeselect,
    vcrontab
  },
  data() {
    return {
      normalizer(node) {
        if (node.children && !node.children.length) {
          delete node.children;
        }
        return {
          id: node.deptId,
          children: node.children,
          label: node.deptName
        };
      },
      cronPopover: false,


      form: {},
      addForm: {
        conferenceName: "",//
        conferenceType: 0,//0:1:
        deptId: null,//
        endTime: "",//()
        isRecord: 0,//01
        master: "",//
        member: "",//(|)
        numWeek: "",//1-4
        password: "",//
        recurringType: "", //012
        remark: "",//
        roundEnd: "",//
        startTime: "", ///()
        seqType: 0, // (0: 1:)
        currentEndTime: "", //
        currentStartTime: "", //
        cron: ""
      },
      list: [],
      time: [], //
      time1: [], //
      memberList: "",
      treeArr: [],
      dialogFormVisible: false,
      formLabelWidth: "120px",
      title: "",
      meetingState: [
        { label: "", value: 0 },
        { label: "", value: 1 },
        { label: "", value: 2 }
      ],
      meetingType: [
        { label: "", value: 0 },
        { label: "", value: 1 }
      ],
      member: [],
      expression: "",
      showCron: false,
      hideComponent: ["year", "second", "min", "hour"],
      isActive: false,
      rules: {
        conferenceName: [{ required: true, message: "", trigger: "blur" }],
        conferenceType: [{ required: true, message: "", trigger: "blur" }],
        deptId: [{ required: true, message: "", trigger: "blur" }],
        seqType: [{ required: true, message: "", trigger: "blur" }],
        icon: [{ required: false, message: "", trigger: "blur" }],
        time: [{ required: false, message: "", trigger: "blur" }],
        password: [{ required: true, message: "", trigger: "blur" }],
        master: [{ required: true, message: "", trigger: "blur" }],
        member: [{ required: true, message: "", trigger: "blur" }],
        time1: [{ required: false, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    find() {
    },
    clear() {
      this.form = this.$options.data().form;
    },
    showAddForm(row, title) {
      this.title = title;
      this.dialogFormVisible = true;
      if (title === "") {
        this.addForm = row;
        this.time.push(this.addForm.startTime, this.addForm.endTime);
        this.member = this.addForm.member.split("|");
        this.expression = this.addForm.cron;
      }
    },
    treeClick() {
    },
    dataChange() {
      this.addForm.currentEndTime = this.time1[1];
      this.addForm.currentStartTime = this.time1[0];
    },
    timeChange() {
      this.addForm.endTime = this.time[1];
      this.addForm.startTime = this.time[0];
    },
    getUserList(deptId) {
      const form = {};
      form.deptId = deptId;
      form.pageSize = 1000000;
      getUserAll(form).then(res => {
        console.log(res);
        this.memberList = res.data.data.records;
      });
    },
    submitForm() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          if (this.title === "") {
            addMeeting(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.getCallMetting(this.form);
                this.$message.success("");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.data.msg);
              }
            });
          } else {
            if (this.title === "") {
              upDataMeeting(this.addForm).then(res => {
                console.log(res);
                if (res.data.code === 200) {
                  this.getCallMetting(this.form);
                  this.$message.success("");
                  this.dialogFormVisible = false;
                } else {
                  this.$message.error(res.data.msg);
                }
              });
            }
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs.addForm.resetFields();
    },
    selectChange() {
      this.addForm.member = this.member.join("|");
    },
    showDialog() {
      this.expression = this.input;// cron  UI 
      this.showCron = true;
    },
    crontabFill(value) {
      //
      this.addForm.cron = value;
    },
    focus() {
      this.isActive = true;
    },
    blur() {
      this.isActive = false;
    },
    aClick() {
      this.$refs.inputs.focus();
    },
    getCallMetting(form) {
      getMeeting(form).then(res => {
        this.$bus.$emit("total", res.data.data.total);
        this.list = res.data.data.records;
      });
    }
  },
  created() {
    getOrganizeList().then(res => {
      this.treeArr = fn(res.data.data);
    });
    this.getCallMetting(this.form);
  },

  watch: {
    addForm: {
      deep: true,
      handler(value) {
        this.getUserList(value.deptId);
      }
    },
    dialogFormVisible(value) {
      if (value) {
        this.getUserList();
      }
    }
  }
};
</script>

